<template>
  <div class="lobby">
    <h5>who's online</h5>
    <p v-for="(member, key) in membersOnline" :key="key">
      <img class="avatar mini" :src='`https://api.adorable.io/avatars/75/${member.avatarId}`'>
    </p>
  </div>
</template>

<script>
import components from "./script/components"
import computed from "./script/computed"
import methods from "./script/methods"
import watchers from "./script/watchers"

export default {
  data() {
    return {
      membersOnline:{}
    };
  },
  components,
  computed,
  methods,
  watchers,
  created(){
    this.$colyseus.joinOrCreate("lobby", this.$store.getters['user/getUserData'])
      .then(room=>{

        room.onMessage("joined", (msg)=>{
          this.membersOnline = msg
        })

        room.onMessage("left", (msg)=>{
          this.membersOnline = msg
        })

      })
      .catch(err=>console.error(err))
  }
};
</script>

<style lang="scss">
    @import "./style";
</style>